@import url(https://fonts.googleapis.com/css?family=Underdog&display=swap);
/* --- Logo --- */

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo {
  color: #3086cd;
  font-size: 3.25rem;
  line-height: 100%;
  font-family: 'Underdog', cursive;
}

.logo span:first-child {
  color: #ffb531;
}

.dog-icon {
  width: 4rem;
  height: 4rem;
  margin: 0 5px;
  background-size: contain;
  background-image: url(/react-inupedia/static/media/shiba.d31860a5.svg);
  background-position-x: center;
  background-position-y: bottom;
  background-repeat: no-repeat;
  -webkit-animation: appear 1.5s ease-out 1;
          animation: appear 1.5s ease-out 1;
}

@-webkit-keyframes appear {
  0%    { background-position-y: 3rem; }
  100%  { background-position-y: 0px; }
}

@keyframes appear {
  0%    { background-position-y: 3rem; }
  100%  { background-position-y: 0px; }
}

.loader-wrapper, .loader-wrapper::before {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.loader-wrapper::before {
  content: '';
  background: white;
  opacity: .95;
  z-index: -1;
}

.loader-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.loading-icon {
  width: 7rem;
  height: 7rem;
  background-size: contain;
  background-image: url(/react-inupedia/static/media/bone.7f319a6d.svg);
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-animation: shake 2.5s infinite ease-in-out;
          animation: shake 2.5s infinite ease-in-out;
}

.loading-text {
  font-size: 3rem;
  margin: 1rem 0 0 0;
  color: lightsteelblue;
  font-family: 'Underdog', cursive;
  animation: shine 1s infinite linear alternate-reverse;
}

.loading-details {
  margin: 0;
  font-size: 1rem;
}

@-webkit-keyframes shake {
  0%    { -webkit-transform: rotateZ(0deg); transform: rotateZ(0deg); }
  25%   { -webkit-transform: rotateZ(40deg); transform: rotateZ(40deg); }
  50%   { -webkit-transform: rotateZ(80deg); transform: rotateZ(80deg); }
  75%   { -webkit-transform: rotateZ(40deg); transform: rotateZ(40deg); }
  100%  { -webkit-transform: rotateZ(0deg); transform: rotateZ(0deg); }
}

@keyframes shake {
  0%    { -webkit-transform: rotateZ(0deg); transform: rotateZ(0deg); }
  25%   { -webkit-transform: rotateZ(40deg); transform: rotateZ(40deg); }
  50%   { -webkit-transform: rotateZ(80deg); transform: rotateZ(80deg); }
  75%   { -webkit-transform: rotateZ(40deg); transform: rotateZ(40deg); }
  100%  { -webkit-transform: rotateZ(0deg); transform: rotateZ(0deg); }
}

@-webkit-keyframes shine {
  0%    { color: lightsteelblue; }
  100%  { color: lightslategray; }
}

@keyframes shine {
  0%    { color: lightsteelblue; }
  100%  { color: lightslategray; }
}

html, body, #root {
  margin: 0;
  width: 100%;
  height: 100%;
}

* {
  box-sizing: border-box;
}

body {
  background: #e6e6e6;
}

/* --- Header --- */
header {
  width: 100%;
}

.breed-selector {
  width: 100%;
  margin: auto;
  max-width: 450px;
}

.breed-selector option:not(.placeholder) {
  text-transform: capitalize;
}

/* --- Page content --- */

main {
  width: 100%;
  min-height: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* --- Image gallery --- */

.breed-gallery {
  flex-grow: 1;
  width: 100%;
  display: flex;
  justify-content: center;
}

.breed-gallery img {
  display: block;
  max-width: 100%;
  border: white .5em solid;
  box-shadow: lightgrey 3px 3px 5px;
}

.breed-gallery .btn-toolbar {
  margin: 1em;
  justify-content: center;
}

.breed-gallery .btn-toolbar button,
.breed-gallery .btn-toolbar button:hover,
.breed-gallery .btn-toolbar button:focus,
.breed-gallery .btn-toolbar button:active {
  border: 0;
  color: white;
}

.breed-gallery .btn-toolbar button span {
  width: 5em;
  display: inline-block;
}

.breed-gallery .btn-toolbar button:not(:last-child) {
  margin-right: 5px;
}

.breed-gallery .btn-toolbar button.prev {
  text-align: left;
  background: #3086cd;
}

.breed-gallery .btn-toolbar button.prev span {
  text-align: right;
}

.breed-gallery .btn-toolbar button.next {
  text-align: right;
  background: #ffb531;
}

.breed-gallery .btn-toolbar button.next span {
  text-align: left;
}

/* --- No results message --- */

.no-results {
  opacity: .5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.no-results .icon {
  width: 10rem;
  height: 10rem;
  display: block;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(/react-inupedia/static/media/dog-house.e14d1fe7.svg);
}

.no-results .text {
  color: black;
  font-size: 1.25rem;
  margin: 1rem 0 0 0;
  font-family: 'Underdog', cursive;
}

